@import "https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap";
*, :after, :before {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: Poppins, sans-serif;
}

header {
  text-align: left;
  justify-content: space-around;
  align-items: center;
  gap: 10px;
  display: flex;
}

.iconHamburguesa {
  cursor: pointer;
  font-size: 2rem;
}

.tancamentMenu {
  cursor: pointer;
  display: inline-block;
}

.navMobil {
  z-index: 100;
  background-color: #f9b233;
  padding: 20px;
  font-size: 2rem;
  display: none;
  position: fixed;
  inset: 0;
}

.navMobil li {
  text-align: center;
  list-style: none;
}

.nav {
  display: none;
}

nav.nav ul {
  gap: 10px;
  padding: 0;
  list-style-type: none;
  display: flex;
}

.nav li {
  align-items: center;
  display: flex;
}

.nav li:first-child:after, .nav li:nth-child(2):after {
  content: " | ";
  color: #f9b233;
  margin-left: 10px;
}

li a {
  color: #000;
  padding-left: 5px;
  text-decoration: none;
}

.hero-image {
  text-align: center;
  color: #fff;
  background-color: #0000;
  background-image: linear-gradient(#00000080, #00000080), url("IMG_1531.7465584a.jpg");
  background-position: center;
  background-repeat: repeat, repeat;
  background-size: cover;
  background-attachment: scroll, scroll;
  background-origin: padding-box, padding-box;
  background-clip: border-box, border-box;
  flex-direction: column;
  justify-content: center;
  height: 80vh;
  display: flex;
  position: relative;
}

.hero-image h1 {
  font-size: 3rem;
}

.hero-image p {
  font-size: 1.5rem;
  font-weight: 200;
  line-height: 1em;
}

.seguent {
  cursor: pointer;
  font-size: 2rem;
}

.container {
  background-color: #fff;
  width: 80%;
  max-width: 1000px;
  margin: 0 auto;
}

.seccio-presentacio {
  margin: 50px 30px;
}

.seccio-presentacio h2 {
  margin-bottom: 30px;
  font-size: 2rem;
}

.seccio-presentacio p {
  font-weight: 200;
}

.seccio-presentacio p:nth-child(2) {
  margin-bottom: 30px;
}

.card {
  text-align: center;
  background-color: #f9b233;
  border-radius: 5px;
  margin: auto auto 50px;
}

.card img {
  object-fit: cover;
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
  width: 100%;
  height: 200px;
}

.card-content {
  padding: 15px;
}

.card h3 {
  color: #000;
  margin: 10px 0;
  font-size: 2rem;
}

.card p {
  color: #000;
  margin-bottom: 20px;
  font-size: .8rem;
  font-weight: 200;
}

.btn {
  color: #fff;
  background-color: #000;
  border-radius: 5px;
  padding: 10px 20px;
  font-weight: 200;
  text-decoration: none;
  transition: background-color .3s;
  display: inline-block;
}

.btn:hover {
  color: #f9b233;
  background-color: #fff;
}

.testimonials {
  text-align: center;
  background-color: #f9b233;
  margin: 100px 0;
  padding: 50px 20px;
}

.testimonial-container {
  flex-wrap: wrap;
  justify-content: center;
  gap: 20px;
  display: flex;
}

.testimonial {
  text-align: center;
  background: #fff;
  border-radius: 5px;
  max-width: 300px;
  margin: 40px 0;
  padding: 20px;
  transition: transform .3s;
  box-shadow: 0 4px 8px #0000001a;
}

.testimonial:hover {
  transform: scale(1.05);
}

.testimonial img {
  object-fit: cover;
  border-radius: 50%;
  width: 80px;
  height: 80px;
  margin-bottom: 10px;
}

.testimonial p {
  color: #000;
  font-style: italic;
}

.testimonial h4 {
  color: #000;
  margin-top: 10px;
  font-weight: bold;
}

footer {
  color: #fff;
  text-align: center;
  background-color: #000;
  padding: 50px;
}

footer ul li {
  margin-bottom: 10px;
  list-style-type: none;
}

footer ul li a {
  color: #fff;
}

.social {
  margin: 20px 0;
}

@media screen and (width >= 768px) {
  footer {
    justify-content: space-between;
    align-items: center;
    display: flex;
  }
}

@media screen and (width >= 1024px) {
  .nav {
    display: block;
  }

  .navMobil {
    display: none !important;
  }

  .iconHamburguesa {
    display: none;
  }

  .hero-image h1 {
    font-size: 5rem;
  }

  .content-cards {
    flex-wrap: wrap;
    gap: 10px;
    display: flex;
  }

  .card {
    flex: 1 0 300px;
  }
}

.detall-image {
  text-align: center;
  color: #fff;
  background-color: #0000;
  background-image: linear-gradient(#00000080, #00000080), url("detalle.031b3192.png");
  background-position: center;
  background-repeat: repeat, repeat;
  background-size: cover;
  background-attachment: scroll, scroll;
  background-origin: padding-box, padding-box;
  background-clip: border-box, border-box;
  flex-direction: column;
  justify-content: center;
  height: 80vh;
  display: flex;
  position: relative;
}

detall-image h1 {
  font-size: 3rem;
}

.detall-image p {
  font-size: 1.5rem;
  font-weight: 200;
  line-height: 1em;
}

section h3 {
  margin: 30px 0;
  font-size: 2rem;
}

section p {
  margin: 30px 0;
}

.seccio-detall {
  flex-direction: column;
  gap: 20px;
  display: flex;
}

.detall-container {
  justify-content: space-between;
  align-items: center;
  gap: 20px;
  display: flex;
}

.detall-container img {
  align-content: center;
  width: 100%;
}

@media (width <= 768px) {
  .detall-container {
    flex-direction: column;
    align-items: center;
  }
}

.video {
  text-align: center;
  margin: 40px 0;
}

video {
  width: 100%;
  max-width: 800px;
}

@media screen and (width >= 1024px) {
  .detall-image h1 {
    font-size: 5rem;
  }
}
